<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-xs-12 col-sm-12 col-md-12 col-lg-6 offset-lg-3 text-center">
        <h1>The Tanks</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12 text-center" v-if="!isMobile()">
        WSDA - move, left click - fire, Q(or mouse wheel) - switch weapon, T - teleportataion to the mouse position
      </div>
      <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12 text-center" v-if="isMobile()">
        Touch on enemies - shot, touchmove - move
      </div>
    </div>
    <div class="row mt-2">
      <div class="col text-center">
       <div class="justify-content-center">
         <table class="table"  v-if="game && game.player">
           <tr>
             <th>Health</th>
             <th>Enemies Killed</th>
             <th></th>
             <th  v-for="(weapon, index) of game.player.weapons">
               {{ weapon.name }}
             </th>
           </tr>
           <tr>
             <td>{{game.player.health}}</td>
             <td>{{game.score}}</td>
             <td>

             </td>
             <td  v-for="(weapon, index) of game.player.weapons" :class="{ reload: weapon && !weapon.isActive }">
               {{ weapon.ammoCount }}
             </td>
           </tr>
         </table>
       </div>
        <canvas width="800" height="600" ref="canvas" id="canvas"></canvas>
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-xs-12 mt-xs-1 col-sm-12 mt-sm-1 col-md-12 col-lg-12 text-center">
        <div class="form-group starter">
          <label>Difficulty</label>
          <select class="form-control mb-4" v-model="difficultyLevel">
            <option :value="0">Easy</option>
            <option :value="1">Normal</option>
            <option :value="2">Hard</option>
          </select>
          <b-button variant="primary" @click="startGame">Start game</b-button>
        </div>
      </div>
    </div>
    <div class="row mt4">
      <div class="col">
        <div v-if="isMobile() && game">
          <b-button variant="primary" @click="switchWeapon">Switch weapon</b-button>
        </div>
      </div>
    </div>
    <div class="modal fade" :class="{ show: isGameOver }" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title text-center">Game Over</h5>
            <button type="button" @click="close" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>
              Enemies killed: {{ game && game.score }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import { Game } from "canvas-game-example-the-tanks";

    export default {
        name: "Main",
        components: {
        },
        data() {
            return {
                game: null,
                isGameOver: false,
                difficultyLevel: 1,
                options: [
                    {
                        addEnemyThresholdMs: 10000,
                        addStaticItemThresholdMs: 1000,
                        staticItemsMaxCount: 50,
                        dynamicItemsMaxCount: 10,
                        playerHealth: 1000,
                        fps: 50,
                        reactionLimit: 1000,
                    },
                    {
                        addEnemyThresholdMs: 5000,
                        addStaticItemThresholdMs: 1500,
                        staticItemsMaxCount: 30,
                        dynamicItemsMaxCount: 20,
                        playerHealth: 700,
                        fps: 50,
                        reactionLimit: 750,
                    },
                    {
                        addEnemyThresholdMs: 3000,
                        addStaticItemThresholdMs: 2000,
                        staticItemsMaxCount: 50,
                        dynamicItemsMaxCount: 30,
                        playerHealth: 500,
                        fps: 50,
                        reactionLimit: 500,
                    }
                ]
            };
        },
        mounted() {

        },

        methods: {

            gameOverHandler() {
                this.isGameOver = true;
            },

            isWeaponActive(weapon) {
                return weapon && weapon.isActive;
            },

            startGame() {
                const self = this;
                const options = self.options[self.difficultyLevel];
                options.gameOverHandler = this.gameOverHandler.bind(this)
                this.game = new Game(options);
                this.game.init(this.$refs.canvas).start();
            },

            close() {
                this.isGameOver = false;
            },

            switchWeapon() {
              if ( this.game ) {
                  this.game.switchPlayerWeapon(1)
              }
            },

            isMobile() {
                if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                    return true
                } else {
                    return false
                }
            }
        }
    };
</script>

<style scoped>
  h4 {
    color: #5a626a;
  }
  p {
    padding-left: 4px;
  }

  canvas {
    border: 1px solid #aaa;
  }

  div.active {
    font-weight: bold;
  }

  .modal.show {
    display: block;
  }

  .reload {
    color: #aaaaaa;
  }

  .table {
    width: 800px !important;
    margin: auto;
  }

  .starter {
    max-width: 300px;
    margin: auto;

  }


</style>
